  .lista_principal {
    display: flex;
    list-style-type: none;
    font-family: 'Arima Madurai', cursive;
    font-size: 15px;
    align-items: center;
  }
  
 
 
  .logo_j {
    width: 20%;
    overflow: hidden;
    margin: 20%;
  }
  
 
  .card_jardin {
    width: 15rem;
    margin: 3%;
    text-align: center;
  }
  
  
  .logo_p {
    width: 20%;
    overflow: hidden;
    margin: 20%;
  }
  
  
  .card_primaria {
    width: 15rem;
    margin: 3%;
    text-align: center;
  }
  
 
  .logo_s {
    width: 20%;
    overflow: hidden;
    margin: 20%;
    transition: 0.5s;
    object-fit: cover;
  }
  
  
  .logo_j:hover {
    transform: scale(1.2);
  }
  
  
  .logo_p:hover {
    transform: scale(1.2);
  }
  
 
  .logo_s:hover {
    transform: scale(1.2);
  }
  
  
  .card_secundaria {
    width: 15rem;
    margin: 3%;
    text-align: center;
  }
  
  
  .card-title {
    color: cadetblue;
  }
  
  /**nuestro colegio**/
  
  .nuestrocolegio {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  
  
  h2 {
    text-align: center;
    color: cadetblue;
  }
  
  
  .jardinn_card_areas {
