  /**areas**/
  
  h3 {
    text-align: right;
    color: cadetblue;
  }
  
  
  p {
    text-align: center;
    padding: 5%;
  }
  
  
  .imgArt {
    width: 100%;
  }
  
  /**proyecto educativo*/
  
  .imgProy{
    width: 100%;
  }
  h4 {
    color: cadetblue;
  }
  
  /**contacto**/

  .form{
    background-color: #fff;
    width: 90%;
    margin: auto;
    max-width: 400px;
    border-radius: 1em;
    padding: 3.5em 1.5en;
  }

  .form__container{
    width: 100%;
    display: grid;
    gap: 1em;
    grid-auto-columns: 100%;
  }
  
  .form__title {
    text-align: center;
    margin-bottom: .4em;

  }

  .form__input--message{
    resize: none;
  }
  /**footer**/
  
  footer {
    background-color: cadetblue;
  
  }
  
  .contenedor-info-footer {
    height: auto;
    padding: 10px;
    width: 100%;
    display: flex;
    padding-bottom: 2%;
    justify-content: space-around;}
  
    .columna {
      width: 30%;
      padding: 10px;
      text-align: center;
  }
  
  .redes-sociales {
      font-size: 1.2rem;
      padding: 8px 20px;
      display: flex;
      justify-content: center;
      gap: 6%;
  }
  
  .copy {
      text-align: center;
      font-size: 1rem;
      padding: 5px;
  }
  
  .row{
    margin: 0;
  }
  
  @media (max-width: 768px) {
    
    .contenedor-info-footer {
      flex-direction: column;
      align-items: center;
    }
  }
  
  @media (max-width: 768px) {
    
    .contenedor-info-footer {
      font-size: 10px;
    }
  }
  
  @media (max-width: 768px) {

    .titulo-info-footer {
      font-size: 14px;
    }
  }